import React, { Component } from 'react';
import MdEditor from './index';
import './styles/compact-editor-index.scss';
import './styles/compact-md-view.scss';

class CompactEditor extends Component {
  constructor (props) {
    super(props)
    this.state = {
      edit: props.edit ==='undefined' ? false : props.showEditor, // determines if editor needs to be rendered
      preview: props.edit ==='undefined' ? true : !props.showEditor, // edit mode or preview mode
      htmlData: ''
    }
    this.dirty = false
    this.edited = props.showEditor ==='undefined' ? false : props.showEditor  
    // 
    // Goes inside redux store
    //     
    window.editors = window.editors || {};
    this.store = window.editors[props.id] = window.editors[props.id] || {};
    this.store.editing = false; // state persistance
    this.store.edited = false; // state persistance
  }
  
  Save () {
    // the markdown is compiled only on preview
    if (this.dirty) {
      this.Preview() // force markdown compile
    } else {
      this.setState({
        edit: false,
        preview: true
      })
      console.log('Saving')
    }
    this.edited = false
    if (this.store) {
      this.store.edited = false
    }
    this.props.onSave(this);
  }

  Preview() {
    this.setState({
      preview: true
    })
    this.dirty = false
    if (this.store){
      this.store.editing = false;
    }
    console.log('Previewing')
  }

  Edit(){
    this.dirty = true
    this.setState({
      edit: true,
      preview: false
    })
    this.dirty = true
    this.edited = true
    if (this.store) {
      this.store.editing = true
      this.store.edited = true
    }
  }

  setValue (compiledHtml) {
    this.setState({
      htmlData: compiledHtml
    })
  }

  componentDidMount () {
    // load the compiled htmlData
    if (this.store) {
      this.setState({
        htmlData: this.store.compiledHtml
      })
    }
    // set prev edition state
    if (this.store) {
      this.setState({
        edit: this.store.editing,
        preview: !this.store.editing
      })
      this.edited = this.store.edited
    }
  }

  componentDidUpdate(prevProps, prevState) {     
    if (this.refs.el) {
      var code = this.refs.el.querySelectorAll('pre code')
      code.forEach(function (el, index) {
        window.hljs.highlightBlock(el)
      })
    }

    // if not previewing the edit mode
    if (!this.state.preview) {
      this.props.onEdit(this)
    }
  }

  render () {
    var headerControlsSave = ''
    if (this.props.nosave !== true && this.edited ===true) {
      headerControlsSave = <li className="c-pointer" onClick={this.Save.bind(this)} >Save</li>
    }

    // if (this.edited && this.state.preview && this.state.edit) {
    //   statusEl = <div className="c-editor-item-u-edit-status status-preview">Preview</div>
    // } else if (this.edited && !this.state.edit) {
    //   statusEl = <div className="c-editor-item-u-edit-status status-saved">Saved</div>
    // } else {
    //   statusEl = <div className="c-editor-item-u-edit-status status-saved"></div>
    // }
    
    // class adder
    var edited = ''
    if (this.edited) {
      edited = 'edited'
    }
    var editing = '',
    previewing = ''
    // here preview is used coz preview is used inside MdEditor
    // preview is used coz in absence of preview we will show preview mode
    if (!this.state.preview) {
      editing = 'active'
      previewing = ''
    } else {
      editing = ''
      previewing = 'active'
    }

    return (
      <div className={"c-editor-item-u-modal " + edited}>
        <div className="c-editor-item-u-header">
          {
            (this.edited ===true) ? (<ul className="c-editor-top-controls c-editor-top-left-controls">
                <li className={"c-pointer " +  editing} onClick={this.Edit.bind(this)} >Edit</li>
                <li className={"c-pointer " + previewing} onClick={this.Preview.bind(this)} >Preview</li>
              </ul>
            ) : ''
          }
          <ul className="c-editor-top-controls">
            {
              (this.edited !== true) ? (<li className="c-pointer" onClick={this.Edit.bind(this)} >Edit</li>) : ''
            }
            { headerControlsSave }
          </ul>
        </div>
        <div className="c-editor-item-u-wrapper">
          { 
            this.state.edit === true ? (
              <div className="c-editor-item-u-editor">
                <MdEditor ref="editor" parent={this} editor={!this.state.preview} id={this.props.id} setValue={this.setValue.bind(this)}></MdEditor>
              </div>
              ) : (
              <div className="c-editor-item-u-content-wrapper" ref="el">
                <div className="c-editor-item-u-content" dangerouslySetInnerHTML={{ __html: this.state.htmlData }}></div>   
              </div>
            )
          }
        </div>

        <div className="c-editor-item-u-footer">
          {this.props.footer}
        </div>
      </div>
    )
  }
}

export default CompactEditor;


