import React, { Component } from 'react';
import { Route, BrowserRouter, Switch, Link } from 'react-router-dom';
import './index.scss'

class SignInUp extends Component {
  render() {
    return (
      <div className="nav-links">
        <p><Link to={`/login`}>Sign in</Link> <span className="or-spacer">or</span> <Link to={`/`}>Sign up</Link></p>
      </div>
    );
  }
}

class SignedIn extends Component {
  render() {
    return (
      <div className="nav-links logged-in-profile">
        <div className="hw-40 c-e3 nav-avatar"></div>
      </div>
    );
  }
}

class Navigation extends Component {
  render () {
    console.log(this)
    return (
      <header>
        <div className="nav-header">
          <div className="layout layout-main">
            <div className="brand-title">
              NoteApp
            </div>
            <BrowserRouter>
              <Switch>
                <Route path="/notebooks" component={SignedIn}/>
                <Route path="/" component={SignInUp}/>
              </Switch>
            </BrowserRouter>
          </div>
        </div>
      </header>      
    )
  }
}

export default Navigation;



