import React, { Component } from 'react';
import _ from 'lodash';

import './styles/index.scss'
const update = _.debounce(function (e, store) {
  // var str = this.refs.editArea.innerText
  // this.setValue(str)
  // this.setState({
  // localStore.set('editor-content', str)
  
  // save to store in markdownText: String
  store.markdownText = e.target.value
  // place new doings in undo stack
  // localStore.set('editor-content', e.target.value)
}, 300)

class _MdTextArea extends Component {
  constructor (props) {
    super(props)
    this.state = {
      input: ''
    }

    this.valueChanged = this.valueChanged.bind(this);

    this.selStartPos = 0
    this.selEndPos = 0
    this.dirty = false
    this.scrollPos = 0
  }

  valueChanged (event) {
    event.persist()
    // without this the data does not update onKey press
    this.setState({input: event.target.value});
    update(event, this.props.parent)
  }

  updateStore (newMarkdown) {
    console.log(newMarkdown)
    // update store.markdownText
    this.props.parent.markdownText = newMarkdown
    if (this.dirty) {
      this.setSelection(this.selStartPos, this.selEndPos)
      this.dirty = false
    }
  }

  keyTab (e) {
    document.execCommand('insertText', false, '\t')
  }
  setSelection (selStart, selEnd) {
    console.log(this)
    var elem = this.refs.editArea
    if (elem != null) {
      if (elem.createTextRange) {
        elem.focus()
        var range = elem.createTextRange()
        range.move('character', selStart)
        range.select()
        range.move('character', selEnd)
      } else {
        if (elem.selectionStart) {
          elem.focus()
          elem.scrollTop = this.scrollPos
          elem.setSelectionRange(selStart, selEnd)
        } else {
          elem.focus()
        }
        console.dir(elem)
      }
    }
  }

  addHeader () {
    this.addMarker('# ', ' #', 'Header', '# '.length, '# '.length + 'Header'.length)
  }

  makeItalic () {
    this.addMarker('*', '*', '')
  }

  addCode () {
    this.addMarker('`', '`', '$', '`'.length, '`'.length + '$'.length)
  }

  addComment () {
    this.addMarker('>', '', '')
  }

  makeBold () {
    this.addMarker('**', '**', 'Bold', '**'.length, '##'.length + 'Bold'.length)
  }

  addLink () {
    this.addMarker('[', '](url)', '')
  }

  addImage () {
    this.addMarker('![Alt text](', ' "Optional title")',
      'https://path_to_file.png',
      '![Alt text]('.length,
      '![Alt text]('.length + 'https://path_to_file.png'.length
    )
  }

  addBulletList () {
    this.addMarker('\n- ', '')
  }

  addTable () {
    var str = '| First Header  | Second Header |\n' +
      '| ------------- | ------------- |\n' +
      '| Content Cell  | Content Cell  |\n' +
      '| Content Cell  | Content Cell  |\n'
    this.addMarker('\n' + str, '', '')
  }

  addHorizontalLine () {
    this.addMarker('\n------\n', '', '')
  }

  addEquation () {
    this.addMarker('$$', '$$', '\\Pi = 3.124', '$$'.length, '$$'.length + '\\Pi = 3.124'.length)
  }

  addMarker (openingTag, closingTag, midToken, selStartPos, selEndPos) {
    var save = ''
    var endPoints = this.getEndPoints()

    if (endPoints.start === endPoints.end) {
      if (midToken === undefined) {
        midToken = ''
      }
      save = this.addTagAtStartAndEnd(openingTag, midToken + closingTag, false)
      this.setState({ input: save })

      if (selStartPos !== undefined || selEndPos !== undefined) {
        if (selStartPos !== undefined && selEndPos !== undefined) {
          this.selStartPos = endPoints.start + selStartPos
          this.selEndPos = endPoints.start + selEndPos
        } else {
          console.error('Not enougn args provided')
        }
      } else {
        this.selStartPos = endPoints.start + openingTag.length
        this.selEndPos = endPoints.start + openingTag.length
      }
      this.scrollPos = this.refs.editArea.scrollTop
      this.dirty = true
    } else { // range selected
      save = this.addTagAtStartAndEnd(openingTag, closingTag, true)
      this.setState({ input: save })
    }
    this.updateStore(save)// update store.markdownText
  } 

  addTagAtStartAndEnd (openingTag, closingTag, range) {
    var endPoints = this.getEndPoints()
    if (!range) {
      var str1 = this.state.input || '';
      var ret1 = str1.slice(0, endPoints.start) + // consider space adjustment
      openingTag +
      closingTag +
      str1.slice(endPoints.end, str1.length)
      return ret1
    } else {
      var str = this.state.input
      var ret = str.slice(0, endPoints.start) +
      openingTag +
      str.slice(endPoints.start, endPoints.end) + // consider space adjustment
      closingTag +
      str.slice(endPoints.end, str.length)
      return ret
    }
  }

  getEndPoints () {
    return {
      start: this.refs.editArea.selectionStart,
      end: this.refs.editArea.selectionEnd
    }
  }

  componentWillMount() {
    this.setState({
       input: this.props.parent.markdownText
    })
  }

  render() {
    return (
      <div className="md-edit-area">
        <div className="md-edit-area">
          <textarea className="md-textarea" ref="editArea" value={this.state.input} onChange={this.valueChanged}></textarea>
        </div>
      </div>
    );
  }
}

export default _MdTextArea;


