import React, { Component } from 'react';

import ToolBar from './components/ToolBar/index'
import MdTextArea from './components/MdTextArea/index'
import MdView from './components/MdView/index'

// import store from './store' // initialize the editor store

window.editors = window.editors || {};

import './styles/index.scss'

/*
 *  Simple Markdown editor
 * 
 *  props:
 *    id: editor-name-idNum   // used for store id
 *    value: Optional:String  // used for parent to child value passing
 *    editor: Boolean         // used for editor/preview mode toggle
 *  
 */

class _MdEditor extends Component {
  constructor (props) {
    super(props)
    this.value = ''
    // save the data to store
    // ctreaete the  store
    
    this.markdownText = ''
    this.compiledHtml = ''
    this.tempMarkdownText = ''

    console.log('Preview', !props.editor)
  }

  setHtmlValue (htmlValue) {
   if(typeof this.props.setValue === 'function') {
      this.props.setValue(htmlValue)
    }
  }

  // componentDidMount() {
  //   if (this.props.editor===true) {
  //     if (this.props.parent && this.props.parent.props.onEdit) {
  //       this.props.parent.props.onEdit(this.props.parent)
  //     }
  //   }
  // }


  render() {
    return (
      <div className="markdown-editor">
        <div className="editor-page">
          <div className="editor-wrapper">
            <div className="editor-layout">
              { this.props.editor===true ?(
                  <div>
                    <ToolBar parent={this}></ToolBar>
                    <MdTextArea ref="textArea" parent={this}></MdTextArea>
                  </div>
                ) : (
                  <div>
                    <MdView ref="mdView" parent={this} setHtmlValue={this.setHtmlValue.bind(this)}></MdView> 
                  </div>
                )
              }
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default _MdEditor;


