import React, { Component } from 'react';
import ContentList from './../ContentList/index'
import './index.scss'

class SidebarContentEditor extends Component {
  addChapter () {
    this.props.parent.state.chapters.forEach((_el)=>{
      _el.selected = false
    })
    var chapters = this.props.parent.state.chapters.concat([{
      id: this.props.parent.state.chapters,
      name: 'New Chapter',
      edit: true,
      selected: true,
      type: 'chapter',
      children: 0
    }])
    chapters.forEach(function(el, index) {
      el.gIndex = index
    });
    this.props.parent.setState({
      chapters: chapters
    })
    this.refs.clist.editEl = chapters.length - 1
    this.refs.clist.editing = true
  }

  render () {
    return (
      <div className="w100p">
        <div className="note-search">Search</div>
        <ContentList ref="clist" children={this.props.chapters} chapters={true} parent={this.props.parent} onSelect={this.props.onSelect}/>
        <span className="add-chapter-btn" onClick={this.addChapter.bind(this)} >Add a chapter</span>
      </div>
    )
  }
}
// {(this.props.chapters.length === 0) ? <div className="add-chapter" onClick={this.addChapter.bind(this)}>+</div> : ''}

export default SidebarContentEditor;


