import React, { Component } from 'react';
import './styles/index.scss'
import  CompactEditor from './../su-md-editor/compact-editor';
import SidebarContentEditor from './SidebarContentEditor/index';
import Note from './../../gapi/note';
import AsyncNote from './../../gapi/async-note';
import handleGauthLoad from './../../gapi/gapi'


class _Notes extends Component {
  constructor (props) {
    super(props)
    var self = this
    this.state = {
      editors: [],
      chapters: [],
      noteEditors: [],
      activeEditor: 0
    }
    if (this.props.match.params.noteId) {
      AsyncNote.note = new Note(this.props.match.params.noteId)
    }
    // handleGauthLoad(()=>{
    //   console.log('Authorized...');
    // })
    // create Note object
    // async.tasks.push({
    //   task: 'CREATE_NEW_NOTE', // also update lIndex
    //   id: ''
    // })    
    // if (!async.running && async.tasks.length !== 0) {
    //   async.run()
    // }

    // parseEditorNo(this.state.chapters, this.state.noteEditors)

  }

  activeLink (id) {
    var chapters = this.state.chapters
    for(var i = 0, length1 = chapters.length; i < length1; i++){
      if (chapters[i].id === id) {
        chapters[i].selected = true;
      } else {
        chapters[i].selected = false;
      }
    }
    this.setState({
      chapters: chapters
    })
  }

  onSelect (q) {
    this.setState({
      activeEditor: parseInt(q, 10)
    })
    this.activeLink(q, this.state.chapters)
  }

  componentDidMount() {
    document.onscroll = function (e) {
      console.log(e)
    }
    // handleGauthLoad()
  }

  render() {
    return (
      <div className="course-notes">
        <div className="">
          <div className="col md-3 ">
            <div className="sidebar-note-content">
              <SidebarContentEditor parent={this} chapters={this.state.chapters} onSelect={this.onSelect.bind(this)}/>
            </div>
          </div>
          <div className="col md-9">
            <div className="note-reading-space">
              <CompactEditor
                parent={this}
                showEditor={false}
                onSave={()=>{}}
                onEdit={()=>{}}
                id={'course-notes-editor-' + this.state.activeEditor}
                key={this.state.activeEditor} />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default _Notes;


