import React, { Component } from 'react';
import { Route, HashRouter as Router, Switch } from 'react-router-dom';

import './App.css';
import './../node_modules/bootstrap/dist/css/bootstrap.min.css' // load bootstrap
import './../node_modules/karicha/styles/css/layout/grid.col.css'
import './../node_modules/karicha/styles/css/layout/layout.css'
import './../node_modules/karicha/styles/css/layout/grid.col.offset.css'
import './../node_modules/karicha/styles/css/helper/border.css'
import './../node_modules/karicha/styles/css/helper/pad.mar.css'
import './../node_modules/karicha/styles/css/helper/colors.css'
import './../node_modules/karicha/styles/css/helper/dimentions.css'
import './../node_modules/karicha/styles/css/util/fonts.css'

import Navigation from './components/navigation/index'
import MainPage from './components/main-page/index'
import NoteBook from './components/su-note-book/index'
import ReadNote from './components/su-note/index'
import LoginPage from './components/login-page/index'
// import CompactEditor from './components/su-md-editor/compact-editor'
// import Notes from './components/su-notes/index';


// <Notes id={'#123123122'}></Notes>
// <Navigation/>
class App extends Component {
  constructor (props) {
    super(props)
    this.data = {
      baseURL: ''
    }
  }

  componentDidMount() {
    // this.baseURL = 'https://holasm.github.io/react-test/noteapp/'
    this.baseURL = 'https://localhost:3000'
  }

  render() {
    return (
      <div className="App">
        <Router basename={this.data.baseURL}>
          <div className="">
            <Switch>
              <Route path={`/notebooks`} component={NoteBook}/>
              <Route path={`/login`} component={LoginPage}/>
              <Route path={`/note/edit/:noteId`} component={ReadNote}/>
              <Route path={`/`} component={MainPage}/>
            </Switch>
          </div>
        </Router>
      </div>
    );
  }
}

export default App;



