import React, { Component } from 'react';

import './styles/mdview.scss'

import showdown from 'showdown'

class _MdView extends Component {
  constructor(props) {
    super(props)
    if (showdown) {
      // showdown.extensions.newline = this.newline
    }
    this.state = {
      compiledHtml: ''
    }
    this.converter = new showdown.Converter()
    window.showdown = new showdown.Converter()
    this.converter.setFlavor('github')
  }

  markdownToHtml (markdownText) {
    var mdText = markdownText
    // if (showdown) {
    //   showdown.extensions.newline = this.newline
    // }

    /*
     * Put the compilation in another file and run using web workers
     * Proposal:
     *  Try to do selective compilatin. compile atmost 100 line at once.
     *  create bucket of atmost 100 lines and compile them and catch them
     *  resue the catched text. recompile the range between which modification is done
     */

    this.converter = this.converter || new showdown.Converter()
    this.converter.setFlavor('github')
    // compile markdown and math equation separately
    mdText = this.compileMath(mdText)

    /* using katex render $$ text $$ */
    // combine and return the result
    // window.katex.renderToString(html)

    var data = this.converter.makeHtml(mdText, { sanitize: true })
    return data    
  }

  compileMath (mdText) {
      var arr = []
      var error = false
      var ret = {}
      var i
      var result
      // var tt = window.katex.renderToString(mdText)
      var reEXP = /(\$\$\$(.|\n)*?\$\$\$)|(\$\$(.|\n)*?\$\$)/ig
      // var reEXP$$Start = /^(?!\\$\$\$)(\$\$\$)/ig
      // var reEXP$$End = /(\$\$\$(.|\n)*?\$\$\$)|(\$\$.*?\$\$)/ig
      // collect the tokens
      while (!error) {
        ret = new window.Object()
        result = reEXP.exec(mdText) // = tempText.slice(result[0].length))
        if (!result) {
          error = true
          break
        }
        ret.match = result[0]
        ret.start = result.index
        ret.end = ret.start + result[0].length
        i = result.length
        while (--i) {
          if (result[i] !== undefined) {
            ret.group = i
          }
        }
        arr.push(ret)
      }

      // process the token and replace in the markdown text
      for (var l = arr.length - 1; l >= 0; l--) {
        var el = arr[l]
        var token = mdText.slice(el.start, el.end)
        var katexCompiled = ''
        if (el.group === 1) { // $$$ __ $$$
          token = token.slice(3).trim(' ')
          token = token.slice(0, token.length - 3).trim(' ')
          try {
            katexCompiled = window.katex.renderToString(token, { displayMode: true })
          } catch (err) {
            console.error(err)
          }
          mdText = this.replaceRange(mdText, el.start, el.end, katexCompiled)
        } else if (el.group === 3) { // $$ __ $$
          token = token.slice(2).trim(' ')
          token = token.slice(0, token.length - 2).trim(' ')
          try {
            katexCompiled = window.katex.renderToString(token)
          } catch (err) {
            console.error(err)
          }
          mdText = this.replaceRange(mdText, el.start, el.end, katexCompiled)
        }
      }

      return mdText
  }

  replaceRange (s, start, end, substitute) {
    return s.substring(0, start) + substitute + s.substring(end)
  }

  afterCompileProcess () {
    // highlight the codes
    var code = this.refs.el.querySelectorAll('pre code')
    code.forEach(function (el, index) {
      window.hljs.highlightBlock(el)
    })
  }

  replaceClass (str) {
  }

  componentDidMount() {
    // call afterCompile method or code syntax highlighting
    this.afterCompileProcess()
  }

  componentWillMount() {
    this.loadMarkDowonFromStore()
  }

  loadMarkDowonFromStore () {
    // load the data from store markdownText: String
    var markdownText = this.props.parent.markdownText
    var html = this.markdownToHtml(markdownText)
    var compiledHtml = this.compileMath(html)
    // compiledHtml = compiledHtml.replaceAll('class=', "className=")

    // save the data to store :compiledHtml: Strign HTML
    this.setState({
      compiledHtml: compiledHtml
    })
    
    // pass data to parent <MdEditor/>
    if(typeof this.props.setHtmlValue === 'function') {
        this.props.setHtmlValue(compiledHtml)
    }

    this.props.parent.compiledHtml = compiledHtml
    this.onRender(compiledHtml)
  }

  setCb (cb) {
    this.onRender = cb
  }
  
  onRender(){}

  render() {
    return (
      <div className="md-view" ref="el">
        <div dangerouslySetInnerHTML={{ __html: this.state.compiledHtml }}></div>
      </div>
    );
  }
}


export default _MdView;


