// import Note from './gapi'

var async = {
  note: {}, // Note Object will be assigned hete
  metadata: {},
  tasks: [],
  running: false,
  run: function () {
    var self = this
    if (self.tasks.length===0) { 
      console.log('All tasks finished...waiting...')
      return
    }
    
    var task = self.tasks.shift()

    var chapters = task.chapters
    
    if (task.task === 'CREATE_NEW_NOTE') {
      // this.note = new Note(task.id)
    } else if (task.task === 'TRY_MOVE_FILE') {
      if (task.fromFolderId !== task.toFolderId) {
        async.tasks.push({
          task: 'STRICT_MOVE_FILE',
          fileId: task.fileId,
          fromFolderId: task.fromFolderId,
          toFolderId: task.toFolderId
        })
      }
      // create the metadata
      self.createMetadata(chapters)
      async.tasks.push({
        task: 'UPDATE_METADATA',
        data: self.metadata
      })
      // self.metadata = metadata
      self.run()
    } else if (task.task === 'CHANGE_CHAPTER_INDEX') {
      // create the metadata
      console.log('Cteating index change metadata')
      
      self.createMetadata(chapters)

      self.tasks.push({
        task: 'UPDATE_METADATA',
        data: self.metadata
      })
      self.run()
    } else if (task.task === 'UPDATE_METADATA') {
      // updating metadata
      // console.log(task.data)
      console.log('Updating metadata')
      self.run()
    } else if (task.task === 'STRICT_MOVE_FILE') {
      // move the file
      // call self.run()when done

      console.log('Moving file from chapter to chapter')
      self.run()
    } else if (task.task === 'UPLOAD_FILE') {
      // register task for file upload
      var data = task.data
      var fileId = task.fileId
      self.run()
    } else if (task.task === 'CREATE_CHAPTER') {
      // create metadata
      console.log('New chapter created')
      // create a chapter
      // update the metadata
      self.run()
    } else if (task.task === 'DELETE_CHAPTER') {
      console.log('Chapter deleted')
      var fileId = task.el.parentId
      // self.note.deleteChapter(fileId, ()={
        self.run()
      // })
      // delete whole chapter with warning
    } else if (task.task === 'CREATE_TOPIC') {
      // create file using chapter id
      console.log('New Topic created');
      var name = task.el.name
      var parentId = task.el.parentId
      // self.note.createTopic(parentId, name, ()=>{
        self.run()
      // })
    } else if (task.task === 'DELETE_TOPIC') {
      // delete file using fileID
      console.log('Topic deleted');
      var fileId = task.el.parentId
      // self.note.deleteTopic(fileId, ()={
        self.run()
      // })
    } else if (task.task === 'RENAME_TOPIC') {
      // rename physical file
      console.log('Topic renamed');
      // create metadata
      self.createMetadata(chapters)
      async.tasks.push({
        task: 'UPDATE_METADATA',
        data: this.metadata
      })
      self.run()
    } else if (task.task === 'RENAME_CHAPTER') {
      // rename physical file
      console.log('Chapter renamed');
      // create metadata
      self.createMetadata(chapters)
      async.tasks.push({
        task: 'UPDATE_METADATA',
        data: self.metadata
      })
      self.run()
    }
  },
  createMetadata: function (chapters) {
    var metadata = {}
    var currentChapterIndex = -1
    var currentChapter = {}
    var children = []
    chapters.forEach(function(_el, _index) {
      metadata.chapters = metadata.chapters || []
      if (_el.type === 'chapter') {
        currentChapter = metadata.chapters[++currentChapterIndex] = {}
        currentChapter.id = _el.id
        currentChapter.index_id = _el.index_id
        currentChapter.name = _el.name
        currentChapter.fileId = _el.fileId
      } else {
        children = currentChapter.topics = currentChapter.topics || []
        children.push({
          id: _el.id,
          folderId: _el.parentId,
          name: _el.name,
          fileId: _el.fileId
        })
      }
    });
    this.metadata = metadata
  }
}

export default async;


