import React, { Component } from 'react';

import './index.scss'

class _MdEditor extends Component {
  addHeader () {
    this.props.parent.refs.textArea.addHeader()
  }

  makeBold () {
    this.props.parent.refs.textArea.makeBold()
  }

  makeItalic () {
    this.props.parent.refs.textArea.makeItalic()
  }

  addCode () {
    this.props.parent.refs.textArea.addCode()
  }

  addComment () {
    this.props.parent.refs.textArea.addComment()
  }

  addLink () {
    this.props.parent.refs.textArea.addLink()
  }

  addImage () {
    this.props.parent.refs.textArea.addImage()
  }

  addBulletList () {
    this.props.parent.refs.textArea.addBulletList()
  }

  addTable () {
    this.props.parent.refs.textArea.addTable()
  }

  addHorizontalLine () {
    this.props.parent.refs.textArea.addHorizontalLine()
  }

  addEquation () {
    this.props.parent.refs.textArea.addEquation()
  }

  render() {
    return (
      <div className="md-toolbar">
        <ul className="md-toolbar">
          <li>
            <div className="ctrl" onClick={this.addHeader.bind(this)}>
              <div className="i glyphicon glyphicon-header"></div>
            </div>
            <div className="downarrow">
              <div className="i glyphicon glyphicon-chevron-down"></div>
            </div>
          </li>
          <li>
            <div className="ctrl" onClick={this.makeBold.bind(this)}>
              <div className="i glyphicon glyphicon-bold"></div>
            </div>
          </li>
          <li>
            <div className="ctrl" onClick={this.makeItalic.bind(this)}>
              <div className="i glyphicon glyphicon-italic"></div>
            </div>
          </li>
          <li>
            <div className="separator"></div>
          </li>
          <li>
            <div className="ctrl" onClick={this.addComment.bind(this)}>
              <div className="i glyphicon glyphicon-comment"></div>
            </div>
          </li>
          <li>
            <div className="ctrl" onClick={this.addCode.bind(this)}>
              <div className="i glyphicon glyphicon-console"></div>
            </div>
          </li>
          <li>
            <div className="ctrl" onClick={this.addLink.bind(this)}>
              <div className="i glyphicon glyphicon-link"></div>
            </div>
          </li>
          <li>
            <div className="ctrl" onClick={this.addImage.bind(this)}>
              <div className="i glyphicon glyphicon-picture"></div>
            </div>
          </li>
          <li>
            <div className="separator"></div>
          </li>
          <li>
            <div className="ctrl" onClick={this.addBulletList.bind(this)}>
              <div className="i glyphicon glyphicon-list"></div>
            </div>
          </li>
          <li>
            <div className="ctrl" onClick={this.addTable.bind(this)}>
              <div className="i glyphicon glyphicon-th"></div>
            </div>
          </li>
          <li>
            <div className="ctrl" onClick={this.addHorizontalLine.bind(this)}>
              <div className="i glyphicon glyphicon-option-horizontal"></div>
            </div>
          </li>
          <li>
            <div className="separator"></div>
          </li>
          <li>
            <div className="ctrl" onClick={this.addEquation.bind(this)}>
              <div className="i glyphicon glyphicon-usd"></div>
            </div> 
          </li>
        </ul>
      </div>
    );
  }
}

export default _MdEditor;


