import React, { Component } from 'react';
import { Link} from 'react-router-dom';

import './index.scss'

import handleGauthLoad from './../../gapi/gapi'
import NoteBook_ from './../../gapi/notebook'


class NoteBook extends Component {
  constructor (props) {
    super(props)
    this.state = {
      gdrive: false,
      notes: []
    }
    this.noteBook = {}
  }

  componentDidMount() {
    var self = this
    window.notebook_ = this
    var notes =  [
      {
        name: 'phy 1.note'
      },{
        name: 'phy 2.note'
      },{
        name: 'phy 3.note'
      },{
        name: 'phy 4.note'
      },{
        name: 'phy 5.note'
      },
    ];

    handleGauthLoad((res)=>{
      self.setState({
        gdrive: true
      })
      console.log(res)
      self.noteBook = new NoteBook_(res.id)

      self.noteBook.init((metadata) => {
        self.setState({
          notes: metadata.notes
        })
      })
    })
  }

  createNote () {
    console.log('Creating a note')
  }

  render() {
    var notes = <h1>Loading</h1>
    if (this.state.notes.length !== 0) {
      notes = this.state.notes.map((note, index)=>{
        var lindex = note.name.lastIndexOf('.')
        var name = note.name.slice(0, lindex)
        console.log(name, note.name)
        return (
          <li className="notebook-li row" key={index}>
            <div className="col md-10">
              <div className="---" > {name} </div>
            </div>
            <div className="col md-1">
              <Link to={`/note/edit/${note.id}`}><div className="k-sm-btn">Edit</div></Link>
            </div>
            <div className="col md-1">
              ooo
            </div>
          </li>
        )
      })
    }
    // <div className="k-sm-btn" onClick={window.signIn}>ooo</div>

    return (
      <div className="note-book">
        <div className="notebook-toolbar">
          <div className="layout">
            <div className="notebook-search">
              <input type="text" className="form-input"/>
            </div>
            <div className="notebook-storage">
              <ul className="options">
                <li className="option-item">
                  <div className="k-sm-btn" onClick={()=>{this.createNote()}}>create note</div>
                </li>
                <li className="option-item">
                  {this.state.gdrive ? <div className="k-sm-btn" onClick={()=>{window.signIn()}}>Gdrive out</div> :
                  <div className="k-sm-btn" onClick={()=>{window.signOut()}}>Gdrive in</div>}
                </li>
              </ul>
            </div>
          </div>
        </div>
        <div className="layout">
          <div className="row">
            <div className="col md-8">
              <ul className="notebook-ul">  
                {notes}
              </ul>
            </div>
            <div className="col md-4">
              ---
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default NoteBook


